<template>
  <card>
    <h4 slot="header" class="card-title">Add News</h4>
    <form>

      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="URL"
                    placeholder="URL"
                    v-model="news.URL">
          </fg-input>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="Tile"
                    placeholder="title"
                    v-model="news.title">
          </fg-input>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="Slug"
                    placeholder="slug"
                    v-model="news.slug">
          </fg-input>
        </div>
      </div>   

      <div class="row">
        <div class="col-md-12">
          <fg-input type="text"
                    label="image URL"
                    placeholder="image URL"
                    v-model="news.image_URL">
          </fg-input>
        </div>
      </div> 

      <div class="text-center">
        <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="updateProfile">
          Add
        </button>
      </div>
      <div class="clearfix"></div>
    </form>
  </card>
</template>
<script>
  import Card from 'src/components/UIComponents/Cards/Card.vue'

  export default {
    components: {
      Card
    },
    data () {
      return {
        news: {
          title: '',
          slug: '',
          URL: '',
          image_URL: '',
          _created_at: '2017-05-11T02:10:43 -03:00',
          _id: 'FFT2323778998xd9'
        }
      }
    },
    methods: {
      updateProfile () {
        // alert('Your data: ' + JSON.stringify(this.news))
        var data = JSON.stringify(JSON.stringify(this.news))
        var xhr = new XMLHttpRequest()
        xhr.withCredentials = true
        xhr.addEventListener('readystatechange', function () {
          if (this.readyState === 4) {
            console.log(this.responseText)
          }
        })
        xhr.open('PUT', 'https://hopeful-einstein-a49a10.netlify.com/api/news')
        xhr.setRequestHeader('secret-key', '$2a$10$16FRVnX.yS6Epo8CXnXhGuLf2dlsGstz2Y00LNAxpOGpan/TW1lp2')
        xhr.setRequestHeader('Content-Type', 'application/json')
        xhr.setRequestHeader('Cache-Control', 'no-cache')
        xhr.setRequestHeader('Postman-Token', '487cb567-c9df-040e-2b9e-fd3ab0d3cd38')
        xhr.send(data)
      }
    }
  }

</script>
<style>

</style>



